#doc-text{
    float:left;
    width:70%;
    padding: 3pt;
    box-sizing: border-box;
    border: 1px solid black;
    overflow-wrap: break-word;
    margin: 1pt 0pt;
}


#doc-text div{
    display: inline;
}


#summary-text{
    float:right;
    width:29.9%;
    padding: 3pt;
    box-sizing: border-box;
    border: 1px solid black;
    overflow-wrap: break-word;
    margin: 1pt 0pt;
}

#summary-text div{
    display: inline;
}

.underlined-word{
    text-decoration: underline;
}

.boldfaced-word{
    font-weight:900;
}

.highlighted-word{
    background-color: yellow;
}

.shadowed-word{
    text-shadow: .03em .03em 0 hsl(200 50% 30%);
}


.btn {
    display: inline-block;
    background: #000;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
    font-family: inherit;
  }
  
  .btn:focus {
    outline: none;
  }
  
  .btn:active {
    transform: scale(0.98);
  }
  
  .btn-block {
    display: block;
    width: 100%;
  }

  .NextStateButton{
    float:right;
    width:9%;
    padding: 3pt 0pt;
    margin:0%;
    box-sizing: border-box;
    font-size: 10pt;
}

#state-Start{
    background-color:rgb(255, 255, 0);
    color:black;
    font-size:larger;
    box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    margin:0.3% 0%;
}

#state-ChooseSpan{
    background-color:brown;
    box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    margin:0.3% 0%;
}


#state-Highlight{
    background-color:darkblue;
    box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    margin:0.3% 0%;
}

#state-ReviseSentence{
    background-color:darkcyan;
    box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    margin:0.3% 0%;
}


#SubmitButton{
    float:right;
    box-sizing: border-box;
    margin:0.3% 0%;

    font-family: 'Ubuntu', sans-serif;
    
    width: 140px;
    height: 40px;
    line-height: 1;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: 1px;
    border: 3px solid green;
    background: #fff;
    color: green;
    border-radius: 40px;
    cursor: pointer;
    overflow: hidden;
    transition: all .35s;
}

#SubmitButton:hover{
    background: green;
    color: #fff;
  }



